:root {
    --primaryColor: hsl(14, 90%, 53%);
    --primaryColorLight: hsl(14,96%, 91%);
    --bgColor:hsl(0, 0%, 100%);
    --formColor: hsl(20, 42%, 87%);
    --whiteColor: hsl(0, 0%, 100%);
    --blackColor: hsl(0, 0%, 18%);
    --greyText: hsl(0,0,51%);
    --inputColor: hsl(330, 12, 79%);
    --cardColor: hsl(300, 3%, 94%);

    // Font ang Typography
    --biggestFontSize: 2.5rem;
    --h1FontSize: 1.6rem;
    --h2FontSize:  1.25rem;
    --h3FontSize: 1rem; 
    --normalFontSize: 0.938rem;
    --smallFontSize: 0.813rem;
    --smallestFontSize: 0.75rem;
    --borderRadius: 3rem;
}

.navbar {
    justify-content: space-between;
    width: 100%;
    padding: 1rem 2rem;
    position: fixed;
    background: rgba(255,255,255, 0.737);
    border-bottom: 1.5px solid rgba(248, 255, 255, 0.588);
    backdrop-filter:blur(5px);
    z-index: 1000;

    .logoDiv {
        justify-content: center;
        color: var(--blackColor);
        span {
            font-size: var(--h3FontSize);
            justify-content: center;
        }
    }

    @media screen and (max-width: 767px) {
        .menu {
            position: absolute;
            width: 70%;
            top: -20rem;
            right: 50%;
            left: 50%;
            transform: translate(-50%);
            background: rgba(255,255,255,0.94);
            padding: 2rem;
            border-radius: 10px;
            transition: .8s ease-in-out;
            backdrop-filter: blur(5px);
            box-shadow: 0 2px 3px 4px rgba(236,213,213, 0.516);

            li {
                font-size: var(--h3FontSize);
                font-weight: 500;
                padding: 1rem 0;
                text-align: center;
                cursor: pointer;
                &:hover {
                    color: var(--primaryColor);
                    border-bottom: 1.5px solid var(--primaryColor);
                }
            }
        }
    }

    .menuIcon, .closeIcon {
        font-size: var(--h1FontSize);
        &:hover {
            color: var(--primaryColor);
            cursor: pointer;
        }
    }
    .menuIcon {
        color: var(--blackColor);
    }

    .closeIcon {
        position: absolute;
        bottom: -15px;
        left: 50%;
        transform: translateX(-50%);
        color: var(--blackColor);
    }

    .showMenu {
        top: 8rem;
    }

}

//Media Queries
@media screen and (min-width: 768px) {
    .navbar {
        .logoDiv {
            span {
                font-size: var(--h2FontSize);
            }
        }

        .closeIcon, .menuIcon {
            display: none;
        }

        .menu {
            ul {
                display: flex;
                gap: 1rem;

                li {
                    font-size: var(--h3FontSize);
                    font-weight: 500;
                    padding: 1rem 0;
                    text-align: center;

                    &:hover {
                        cursor: pointer;
                        color: var(--primaryColor);
                        border-bottom: 1.5px solid var(--primaryColor);
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 1024px) {
    .navbar {
        padding: 1.5rem 6rem;
        
        .menu {
            ul {
                gap: 4rem;
            }
        }
    }
}

@media screen and (min-width: 1440px) {
    .navbar {
        padding: 2rem 14rem;
    }
}

