:root {
  --primaryColor: hsl(14, 90%, 53%);
  --primaryColorLight: hsl(14,96%, 91%);
  --bgColor:hsl(0, 0%, 100%);
  --formColor: hsl(20, 42%, 87%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --greyText: hsl(0,0,51%);
  --inputColor: hsl(330, 12, 79%);
  --cardColor: hsl(300, 3%, 94%);
  --biggestFontSize: 2.5rem;
  --h1FontSize: 1.6rem;
  --h2FontSize: 1.25rem;
  --h3FontSize: 1rem;
  --normalFontSize: 0.938rem;
  --smallFontSize: 0.813rem;
  --smallestFontSize: 0.75rem;
  --borderRadius: 3rem;
}

.accordionContainer .title {
  font-size: var(--normalFontSize);
  align-items: center;
  padding: 1rem;
  border-radius: 10px;
  justify-content: space-between;
  background: var(--primaryColorLight);
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
}
.accordionContainer .title span {
  cursor: pointer;
}
.accordionContainer .title span .icon {
  font-size: 1.4rem;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
}
.accordionContainer .description {
  font-size: var(--smallFontSize);
  display: flex;
  align-items: center;
  line-height: 20px;
  white-space: pre-line;
  height: 0;
  transition: all 0.3s ease-in-out;
  opacity: 0;
  padding: 0 1rem;
  background: var(--cardColor);
  border-radius: 0 0 10px 10px;
  color: var(--blackColor);
}
.accordionContainer .show {
  height: 140px;
  opacity: 1;
}
.accordionContainer .activeTitle {
  color: var(--whiteColor);
  background: var(--primaryColor);
  border-radius: 10px 10px 0 0;
}
.accordionContainer .white {
  color: var(--whiteColor);
}

.questions .secHeading h3, .questions .secHeading h4 {
  padding-bottom: 1.5rem;
}
.questions .secHeading p {
  font-size: var(--smallFontSize);
  color: var(--blackColor);
  padding-bottom: 1rem;
}
.questions .form .formContent {
  gap: 1rem;
}
.questions .form .formContent input, .questions .form .formContent textarea {
  background: var(--cardColor);
  width: 100%;
  padding: 1rem;
  border: none;
  outline: none;
  border-radius: var(--borderRadius);
}
.questions .form .formContent input::-moz-placeholder, .questions .form .formContent textarea::-moz-placeholder {
  font-size: var(--smallFontSize);
  color: var(--blackColor);
}
.questions .form .formContent input::placeholder, .questions .form .formContent textarea::placeholder {
  font-size: var(--smallFontSize);
  color: var(--blackColor);
}
.questions .form .formContent textarea {
  height: 100px;
  border-radius: 10px;
}
.questions .form .formContent .btn {
  width: 100%;
}

@media screen and (min-width: 640px) {
  .accordionContainer .title {
    font-size: var(--h3FontSize);
  }
  .accordionContainer .title span .icon {
    font-size: 1.5rem;
  }
  .accordionContainer .description {
    font-size: var(--normalFontSize);
  }
  .accordionContainer .show {
    height: 150px;
  }
  .questions .secHeading h3, .questions .secHeading h4 {
    font-size: var(--h2FontSize);
  }
  .questions .secHeading p {
    font-size: var(--normalFontSize);
  }
  .questions .secContainer {
    grid-template-columns: repeat(2, 1fr);
    align-items: flex-start;
    gap: 2rem;
  }
  .questions .secContainer .form .formContent input, .questions .secContainer .form .formContent textarea {
    font-size: var(--normalFontSize);
  }
  .questions .secContainer .form .formContent input::-moz-placeholder, .questions .secContainer .form .formContent textarea::-moz-placeholder {
    font-size: var(--normalFontSize);
    color: var(--blackColor);
  }
  .questions .secContainer .form .formContent input::placeholder, .questions .secContainer .form .formContent textarea::placeholder {
    font-size: var(--normalFontSize);
    color: var(--blackColor);
  }
  .questions .secContainer .form .formContent textarea {
    height: 200px;
    border-radius: 10px;
  }
}
@media screen and (min-width: 1024px) {
  .questions .secHeading h3, .questions .secHeading h4 {
    font-size: 1.5rem;
  }
  .questions .secHeading p {
    font-size: var(--h3FontSize);
  }
  .questions .secContainer {
    gap: 3rem;
  }
}/*# sourceMappingURL=Questions.css.map */