:root {
    --primaryColor: hsl(14, 90%, 53%);
    --primaryColorLight: hsl(14,96%, 91%);
    --bgColor:hsl(0, 0%, 100%);
    --formColor: hsl(20, 42%, 87%);
    --whiteColor: hsl(0, 0%, 100%);
    --blackColor: hsl(0, 0%, 18%);
    --greyText: hsl(0,0,51%);
    --inputColor: hsl(330, 12, 79%);
    --cardColor: hsl(300, 3%, 94%);

    // Font ang Typography
    --biggestFontSize: 2.5rem;
    --h1FontSize: 1.6rem;
    --h2FontSize:  1.25rem;
    --h3FontSize: 1rem; 
    --normalFontSize: 0.938rem;
    --smallFontSize: 0.813rem;
    --smallestFontSize: 0.75rem;
    --borderRadius: 3rem;
}

.home {
    position: relative;
    height: 100vh;
    width: 100%;
    top: 0;

    .videoBg{
        position: absolute;
        height: 100%;
        width: 100%;
        video {
            height: 100%; 
        }
    }

    .sectionText {
        position: absolute;
        height: 100%;
        top: 20%;
        bottom: 50%;
        color: var(--whiteColor);
        padding: 0 1.3rem;

        h1 {
            font-size: var(--biggestFontSize);
            font-weight: 900;
        }

        p {
            padding: 1rem 0;
            font-size: var(--normalFontSize);
            font-weight: 500;
        }

        .btn {
            gap: .5rem;
            padding-left: 2rem;
            padding-right: 2rem;
            font-size: var(--h3FontSize);
        }
    }

    .popularPlaces {
        display: none;
    }
}

//Media Queries
@media screen and (min-width: 480px) {
    .home {
        .sectionText {
            padding: 0 2rem ;
            justify-content: center;
            text-align: center;
            width: 100%;

            h1 {
                font-size: var(--biggestFontSize);
                font-weight: 900;
            }

            p {
                padding: 1rem 0;
                font-size: var(--h2FontSize);
                max-width: 70%;
                margin: auto;
            }

            .btn {
                font-size: var(--h3FontSize);
                margin: auto;
            }
        }
    }
}

@media screen and (min-width: 768px) {
    .home {
        .sectionText {
            width: 100%;

            h1 {
                font-size: 4rem;
                font-weight: 900;
                max-width: 70%;
                margin: auto;
            }

            p {
                padding: 1rem 0;
                font-size: var(--h2FontSize);
                max-width: 70%;
                margin: auto;
            }

            .btn {
                gap: .5rem;
                padding-left: 2rem;
                padding-right: 2rem;
                font-size: var(--h3FontSize);
                margin: auto;
            }
        }
    }
}

@media screen and (min-width: 1024px) {
    .home {
        position: relative;

        .sectionText {

            h1 {
                font-size: 4rem;
                font-weight: 900;
                max-width: 70%;
                margin: auto;
            }

            p {
                padding: 1rem 0;
                font-size: var(--h2FontSize);
                max-width: 70%;
                margin: auto;
            }

            .btn {
                gap: .5rem;
                padding-left: 2rem;
                padding-right: 2rem;
                font-size: var(--h3FontSize);
                margin: auto;
            }
        }

        .popularPlaces {
            display: block;
            position: absolute;
            width: 100%;
            bottom: 6%;
            display: flex;
            justify-content: center;
            align-items: center;

            h3 {
                color: var(--whiteColor);
                padding: 1rem 0;
                position: relative;

                &::after {
                    content: '';
                    position: absolute;
                    width: 82%;
                    height: 2px;
                    background: rgb(202, 202, 202);
                    right: 0;
                    bottom: 50%;
                }
            }

            .images {
                align-items: center;
                justify-content: center;
                gap: 2rem;

                img {
                    height: 100px;
                    width: 210px;
                    object-fit: cover;
                    border-radius: 10px;
                }
            }
        }

    }
}